}
g.axis path,g.axis line{stroke:#9E9E9E
}
g.axis text{fill:#9E9E9E
}
g.axis.yaxis .tick *,g.axis.xaxis .tick *{stroke:none
}
.transparent{stroke-opacity:.2
}
circle.points{fill:#FF7043
}


