
.wrapper{height:20px;width:200px;margin:10px;padding:10px 0;border:1px solid rgba(0,0,0,0)
}
.wrapper:hover{border:1px solid rgba(255,255,255,0.3)
}
.left{float:left;height:20px;margin-right:10px;margin-left:5px
}
.left .label{font-size:10px;display:inline-block;height:100%;vertical-align:middle;text-align:center;color:#FFFFFF;float:left;font-size:14px;padding:0 5px;width:40px
}
.left .label p{float:right;margin:0;margin-top:1px
}
.left .slider{height:20px;margin:0;width:70px
}
.right .text input{float:left;border:none;padding:0;color:#FFFFFF;background-color:rgba(0,0,0,0);width:50px;height:20px;font-size:14px;line-height:0;vertical-align:middle;text-align:center;margin:0 2px
}
.right .text input:hover{color:#FFFFFF;border-bottom:1px solid #FFFFFF
}
.right .text input:focus{outline:none
}
.right .text input::-moz-selection{background-color:rgba(0,0,0,0.3);padding:10px
}
.right .text input::selection{background-color:rgba(0,0,0,0.3);padding:10px
}
.right .buttons{float:left
}
.right .buttons input{width:10px;height:10px;line-height:0;border:none;padding:0;display:block;font-size:10px;color:#333333
}
.right .buttons input:hover{background-color:#ccc
}
.right .buttons input:focus{outline:none
}


